.vol_anketa_fields_item {
  display: grid;
  grid-template-columns: auto auto;
  gap: 50px;
  justify-content: space-between;
  align-items: center;
  &:not(:last-child) {
    margin-bottom: 15px;
    padding-bottom: 15px;
    border-bottom: 1px solid #9e9e9e;
  }
  &_title {
    font-size: 18px;
    font-weight: 500;
    max-width: 500px;
    color: #333333;
  }
  &_value {
    font-size: 18px;
    font-weight: 400;
    max-width: 400px;
    text-align: end;
    color: #898989;
  }
}
